@import url("https://fonts.googleapis.com/css2?family=Work+Sans&display=swap");

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Work Sans", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.static-about {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #f2d0a9;
  top: 0%;
  left: 0%;
}

.static-about a {
  font-size: 3.25rem;
  font-weight: 700;
}

.rotate-about {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: bottom left;
  transform: rotate(90deg);
}

.rotate-back-about {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  color: #f2d0a9;
  transform-origin: bottom left;
  transform: rotate(0deg);
}

.static-projects {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #99c1b9;
  top: 0%;
  right: 0%;
}

.static-projects a {
  font-size: 3.25rem;
  font-weight: 700;
}

.rotate-projects {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: bottom right;
  transform: rotate(-90deg);
}

.rotate-back-projects {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  color: #99c1b9;
  transform-origin: bottom right;
  transform: rotate(0deg);
}

.static-skills {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #d88c9a;
  bottom: 0%;
  left: 0%;
}

.static-skills a {
  font-size: 3.25rem;
  font-weight: 700;
}

.rotate-skills {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: top left;
  transform: rotate(-90deg);
}

.rotate-back-skills {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  color: #d88c9a;
  transform-origin: top left;
  transform: rotate(0deg);
}

.static-contacts {
  position: fixed;
  margin: 20px;
  cursor: pointer;
  z-index: 5;
  color: #8e7dbe;
  bottom: 0%;
  right: 0%;
}

.static-contacts a {
  font-size: 3.25rem;
  font-weight: 700;
}

.rotate-contacts {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: top right;
  transform: rotate(90deg);
}

.rotate-back-contacts {
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  color: #ffffff;
  transform-origin: top right;
  color: #8e7dbe;
  transform: rotate(0deg);
}

#mobile-navbar {
  width: 100vw;
  height: 60px;
  display: flex;
  color: #ffffff;
  background: #d88c9a;
  background: linear-gradient(270deg, #8e7dbe 0%, #99c1b9 33%, #d88c9a 66%, #8e7dbe 100%);
}

#mobile-navbar ul {
  width: 100%;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#mobile-navbar ul li {
  margin: 0 20px;
  cursor: pointer;
}

#mobile-navbar ul li:hover {
  color: #302e2e;
}

#content {
  margin-top: 15vh;
  max-width: 840px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color: #f1e3d3;
}

#about-me-wrapper {
  padding-top: 10vh;
  font-size: 2.5rem;
  line-height: 3.5rem;
  text-align: center;
  color: #302e2e;
}

#projects-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#project-items {
  max-width: 932px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow: hidden;
}

.project-item {
  background-color: transparent;
  width: 400px;
  height: 400px;
  margin: 50px 5px;
  perspective: 800px;
  cursor: pointer;
}

.project-item-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: all 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.project-item:hover .project-item-inner {
  transform: rotateY(180deg);
}

.project-item-front,
.project-item-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.project-item-front img,
.project-item-back img {
  max-width: 100%;
  width: 100%;
  height: auto !important;
}

.project-item-front {
  outline: 4px solid #f1e3d3;
  outline-offset: -4px;
}

.project-item-back {
  background-color: #f1e3d3;
  color: #302e2e;
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#skills-page-wrapper {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #302e2e;
}

.skills-bar {
  margin: 25px 0px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  flex-wrap: wrap;
}

.skill-wrapper {
  height: 120px;
  width: 120px;
  margin: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e7e7e7;
  border: 2px solid grey;
  border-radius: 4px;
  color: grey;
  transition: all 0.2s linear;
}

.skill-wrapper p {
  font-size: 1.25rem;
}

.skill-wrapper:hover {
  color: black;
  transform: scale(1.3);
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

#contacts-page-wrapper {
  padding: 100px 0;
  font-size: 2rem;
  line-height: 3.5rem;
  color: #f1e3d3;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#contacts-page-wrapper hr {
  width: 50%;
  height: 4px;
  color: #f1e3d3;
  background-color: #f1e3d3;
}

#social-media-bar {
  width: 60%;
  height: 60px;
  background-color: #8e7dbe;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.social-media {
  width: 50px;
  height: 50px;
  position: relative;
}

.social-media i {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 32px;
  color: #f1e3d3;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
  background-color: transparent;
  z-index: 2;
}

.social-media:hover {
  cursor: pointer;
}

.spinning-social-media {
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: 5px solid #8e7dbe;
  border-left-color: #f1e3d3;
  border-right-color: #f1e3d3;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.5s linear;
  transform-style: preserve-3D;
  z-index: 999;
}

.rotate-social-media {
  transform: rotate(180deg);
}

@media (min-width: 992px) and (max-width: 1199.98px) {
  .project-item {
    transform: scale(0.8);
  }

  #skills-page-wrapper {
    max-width: 640px;
  }
}

@media (min-width: 480px) and (max-width: 991px) {
  #content {
    margin: 20px auto;
    font-size: 1.5rem;
  }

  #projects-wrapper {
    text-align: center;
  }

  #projects-wrapper h2 {
    padding: 5px;
  }

  #projects-wrapper .project-item {
    transform: scale(0.75);
    margin: 10px 0px;
  }

  #skills-page-wrapper {
    text-align: center;
  }
}

@media (max-width: 479px) {
  #content {
    margin-top: 2vh;
    font-size: 1.25rem;
  }

  #about-me-wrapper {
    font-size: 1.5rem;
  }

  #projects-wrapper {
    text-align: center;
    max-width: 100vw;
  }

  #projects-wrapper h2 {
    padding: 5px;
  }

  #projects-wrapper .project-item {
    transform: scale(0.75);
    margin: 0px;
    width: 300px;
    height: 300px;
  }

  #projects-wrapper .project-item img {
    max-width: 100%;
    max-height: 100%;
  }

  #skills-page-wrapper {
    text-align: center;
    max-width: 90vw;
  }

  #skills-page-wrapper .skill-wrapper {
    height: 90px;
    width: 90px;
    margin: 6px;
  }

  #skills-page-wrapper .skill-wrapper P {
    font-size: 1rem;
  }

  #skills-page-wrapper .skill-wrapper img {
    max-width: 70%;
    max-height: 70%;
  }

  #contacts-page-wrapper {
    font-size: 1.5rem;
  }

  #contacts-page-wrapper #social-media-bar {
    width: 80%;
    justify-content: space-between;
  }
}